import React from 'react';
// import PropTypes from 'prop-types';
import {} from './redux/hooks';
import { useParams } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { Header, Footer } from '../common/';
import products from '../common/products.json';
// console.log(products);

const useStyles = makeStyles(theme => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  productContent: {
    backgroundColor: theme.palette.background.paper,
    padding: '64px 0px 10px',
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  actionButtons: {
    marginTop: '20px',
    marginBottom: '60px',
  },
  productGrid: {
    paddingTop: '60px',
    paddingBottom: '30px',
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
}));

export default function Detail(props) {
  const classes = useStyles();
  const { id } = useParams();
  const product = products.filter(p => p.id.toString() === id);
  // console.log(product);

  return (
    <React.Fragment>
      <CssBaseline />
      <Header />
      <main>
        <div className={classes.productContent}>
          <Container maxWidth="sm">
            <Typography variant="h5" align="center" color="textSecondary" paragraph>
              <span role="img" aria-label="Detail">
                🔎{' '}
              </span>
              产品{id}的详细内容
            </Typography>
          </Container>
        </div>
        <Container className={classes.productGrid} maxWidth="md">
          <Grid container spacing={4}>
            {product && product.length > 0 && (
              <div style={{ textAlign: 'center', width: '100%' }}>
                <h2>{product[0].name}</h2>
                <h2>{product[0].desc}</h2>
                <p>
                  <img
                    src={'/images/' + product[0].image}
                    alt={product[0].name}
                    style={{ width: '500px' }}
                  />
                </p>
                <p></p>
                {/* <p>{JSON.stringify(product[0])}</p> */}
              </div>
            )}
          </Grid>
        </Container>
        <div className={classes.actionButtons}>
          <Grid container spacing={2} justify="center">
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  console.log(`返回首页`);
                  props.history.push(`/`);
                }}
              >
                返回首页
              </Button>
            </Grid>
            {/* <Grid item>
                  <Button variant="outlined" color="primary">
                    关于我们
                  </Button>
                </Grid> */}
          </Grid>
        </div>
      </main>
      <Footer />
    </React.Fragment>
  );
}

Detail.propTypes = {};
Detail.defaultProps = {};
